import React from "react"
import style from "./paradontologija.module.scss"


const Paradontologija = () => (
    <div className={style.wraper}>
        <div className={style.blue}>
        <h2>PARADONTOLOGIJA</h2>
        </div>
        <div className={style.main}>
            <div className={style.left}>
                <p>Gotovo 60 - 70% sastava ovih materijala je staklo i sitni komadići kvarca a samo 30-40% čini tzv.
                kompozit. Ova smesa, zahvaljujući svojoj mekoći, pre vezivanja omogućuje da se ovakav ispun
                može lepo smestiti u zub. Danas se najcešce koriste tzv. svetlosno polimerizujući kompozitni
                materijali ili UV (ultra ljubičasto) stvrdnjavajući materijali. Ovi se materijali prilikom stvrdnjavanja
                malo skupljaju. Ispuni od kompozita su i estetski lepi pošto se proizvode u prirodnim bojama zuba.
                Zato su rašireni u upotrebi. Ovi materijali su svakako i jeftiniji nego ispuni koji se izrađuju u zubnim
                tehnikama (inleji). Moderni kompozitni materijali kao i ispuni, ukoliko nisu previše veliki, traju i podjednako
                su dobri kao i amalgamski ispuni. <br/></p>
                <h3>Primena i ogranicenja</h3>
                <p>Kompoziti postoje više od 20 godina i pronašli su značajno mesto u terapiji karijesa, posebno kod
                prednjih zuba. Velike probleme za prve kompozite predstavljali su ispuni kod bočnih zuba, jer su tu
                sile žvakanja mnogo vee. Pritisak koji jedan ispun treba da izdrži u bočnoj regiji je veliki. U početku
                kompoziti nisu mogli ispuniti kriterijume za takav zadatak. Sa novim materijalima rešeni su i ovi
                početni problemi. Ali ne može se svaka rupa na zubu ispuniti kompozitnim ispunom. Što je veća
                rupa (kavitet, kažu stomatolozi) time se kompozit teže zadržava na svom mestu. Za što bolje držanje
                kompozitima je potrebno što više tvrde zubne supstance oko sebe.</p>
                <h3>Kako se postavlja jedan kompozitni ispun?</h3>
                <p>Danas se kompoziti, najcešce, postavljaju tzv. višeslojnom tehnikom, jer se pokazalo da se na ovj
                nacin obezbeđuje duže tranjanje ispuna. Prvo se, nakon odstranjivanja karijesa, zidovi obrađene
                rupe (kaviteta) nagrizaju blagom kiselinom. Na ovaj način će se mnogo bolje zalepiti specijalni
                lepak kojim vežemo kompozit za zub. Za sve vreme postavljanja ispuna pljuvačka ne sme da dođe
                u kontakt sa zubom. Neki stomatolozi iz tog razloga koriste specijalne gumene folije kako bi
                izolovali zub od mogućeg dodira sa pljuvačkom. Nakon toga kompozit se postavlja u tankim slojevima
                i svaki sloj se osvetljava specijalnom lampom sa plavom svetlošcu. Na kraju, ispun se dodatno
                obrađuje kako bi dobio oblik prirodnog zuba i polira se da bude gladak.</p>
            </div>
            <div className={style.photo}>
                IMG
            </div>
        </div>

    </div>

)

export default Paradontologija
